import React, { Component, Fragment } from "react";
import isTouchDevice from 'is-touch-device';

import {
	Route,
	NavLink,
	BrowserRouter
} from "react-router-dom";

import Home from "./pages/home";
import Seed from "./pages/seed";
import About from "./pages/about";
import Information from "./pages/information";
import Team from "./pages/team";
import Partners from "./pages/partners";
import Contacts from "./pages/contacts";
import ScrollArea from "react-scrollbar";
import Footer from "./footer";
import Sunflower from "./pages/seed/sunflower";
import Soy from "./pages/seed/soy";
import Raps from "./pages/seed/raps";
import BarleyWinter from "./pages/seed/barley_winter";
import WheatSpring from "./pages/seed/wheat_spring";
import WheatWinter from "./pages/seed/wheat_winter";
import BarleySpring from "./pages/seed/barley_spring";
import Clients from "./pages/partners/clients";
import Outspread from "./pages/partners/outspread";
import Distributors from "./pages/partners/distributors";
import { slide as Menu } from 'react-burger-menu'

function ScrollOnDesktop({children}) {
	if (isTouchDevice()) {
		return (
			<Fragment>
				{children}
			</Fragment>
		);
	}

	const scroll_options = {
		speed: 1,
		className:"area",
		contentClassName:"scroll-area",
		vertical:true,
		horizontal:false,
		smoothScrolling:false,
		style: {
			height: '100%'
		}
	};

	return (
		<ScrollArea {...scroll_options}>
			{children}
		</ScrollArea>
	);
}

class SeedsSubmenu extends Component {
	render() {
		return (
			<ul className={"submenu"}>
				<li className="submenu-item"><NavLink replace to="/seeds/wheat/winter">Пшениця озима</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/seeds/wheat/spring">Пшениця яра</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/seeds/soy">Соя</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/seeds/sunflower">Соняшник</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/seeds/barley/spring">Ячмінь ярий</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/seeds/barley/winter">Ячмінь озимий</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/seeds/raps">Ріпак</NavLink></li>
			</ul>
		)
	}
}

class PartnersSubmenu extends Component {
	render() {
		return (
			<ul className="submenu">
				<li className="submenu-item"><NavLink replace to="/partners/distributors">Офіційні дистрибютори</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/partners/outspread">Партнери з розмноження</NavLink></li>
				<li className="submenu-item"><NavLink replace to="/partners/clients">Клієнти</NavLink></li>
			</ul>
		);
	}
}

class Main extends Component {
	render() {
		return (
			<BrowserRouter>
				<ScrollOnDesktop>
					<div className="main">
						<div className="top-bar">
							<div id="outer-container">
			  					<Menu right width={ '200px' } >
									<ul className="nav-mobile">
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/" strict exact>Головна</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/seeds">Насіння</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/about">Про нас</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/team">Команда</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/partners">Партнери</NavLink></li>
										<li className="menu-item"><NavLink onClick={() => this.closeMenu()} to="/contacts">Контакти</NavLink></li>
									</ul>
								</Menu>
							</div>
							<div className="content row">
								<NavLink to="/" strict exact>
									<span className="logo" />
								</NavLink>
								<ul className="menu">
									<li className="menu-item"><NavLink to="/" strict exact>Головна</NavLink></li>
									<li className="menu-item"><NavLink to="/seeds">Насіння</NavLink></li>
									<li className="menu-item"><NavLink to="/about">Про нас</NavLink></li>
									<li className="menu-item hide"><NavLink to="/information">Демо Поля</NavLink></li>
									<li className="menu-item"><NavLink to="/team">Команда</NavLink></li>
									<li className="menu-item"><NavLink to="/partners">Партнери</NavLink></li>
									<li className="menu-item"><NavLink to="/contacts">Контакти</NavLink></li>
								</ul>
							</div>
						</div>

						<Route path="/seeds" component={SeedsSubmenu}/>
						<Route path="/partners" component={PartnersSubmenu}/>

						<Route exact path="/" component={Home}/>
						<Route exact path="/seeds" component={Seed}/>
						<Route path="/about" component={About}/>
						<Route path="/information" component={Information}/>
						<Route path="/team" component={Team}/>
						<Route exact path="/partners" component={Partners}/>
						<Route path="/contacts" component={Contacts}/>

						<Route path="/seeds/wheat/winter" component={WheatWinter}/>
						<Route path="/seeds/wheat/spring" component={WheatSpring}/>
						<Route path="/seeds/soy" component={Soy}/>
						<Route path="/seeds/sunflower" component={Sunflower}/>
						<Route path="/seeds/barley/spring" component={BarleySpring}/>
						<Route path="/seeds/barley/winter" component={BarleyWinter}/>
						<Route path="/seeds/raps" component={Raps}/>

						<Route exact path="/partners/distributors" component={Distributors}/>
						<Route path="/partners/outspread" component={Outspread}/>
						<Route path="/partners/clients" component={Clients}/>
					</div>
					<Footer />
				</ScrollOnDesktop>
			</BrowserRouter>
		);
	}
}

export default Main;


